export default {
    name: 'LayoutDefault',

    data() {
        return {
            navDrawerOpen: false,
            tab: 'map',
          adminDialog: false,
          adminCode: ''
        }
    },
  methods: {
      adminClick(down) {
        if(down) {

          this.adminClickStartTime = Date.now();
        } else {
          if(Date.now() - this.adminClickStartTime > 1000) {
            this.adminDialog = true;
          }

        }
      }
  }
}
